@font-face {
  font-display: swap;
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/alumni-sans-600.woff2") format("woff2"), url("../fonts/alumni-sans-600.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/alumni-sans-700.woff2") format("woff2"), url("../fonts/alumni-sans-700.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 800;
  src: url("../fonts/alumni-sans-800.woff2") format("woff2"), url("../fonts/alumni-sans-800.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/roboto-condensed-300.woff2") format("woff2"), url("../fonts/roboto-condensed-300.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-condensed-400.woff2") format("woff2"), url("../fonts/roboto-condensed-400.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/roboto-condensed-700.woff2") format("woff2"), url("../fonts/roboto-condensed-700.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  font-family: "Roboto Condensed", "Gill Sans", "Arial", sans-serif;
  color: #102654;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1199px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 27px 44px;
  font-weight: 700;
  font-size: 12px;
  line-height: 12px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.6px;
  background-color: rgba(255, 255, 255, 0);
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  pointer-events: none;
}
.btn--white {
  color: #ffffff;
  border: 4px solid #ffffff;
}
@media (hover: hover) {
  .btn--white:hover:not(.focus-visible) {
    color: #103aae;
    background-color: #ffffff;
  }
}
.btn--white.focus-visible:focus {
  color: #103aae;
  background-color: #ffffff;
}
.btn--white:active:not(.focus-visible) {
  color: #102654;
}
.btn--white:disabled {
  color: #ffffff;
  background-color: rgba(255, 255, 255, 0);
  opacity: 0.5;
}
.btn--disabled-white {
  color: #ffffff;
  background-color: rgba(255, 255, 255, 0);
  opacity: 0.5;
}
.btn--blue {
  color: #ffffff;
  border: 2px solid #1c3374;
}
@media (hover: hover) {
  .btn--blue:hover:not(.focus-visible) {
    color: #ffffff;
    background-image: linear-gradient(151deg, #ed0233 0%, #003eb7 100%);
    border: none;
  }
}
.btn--blue.focus-visible:focus {
  color: #ffffff;
  background-image: linear-gradient(151deg, #ed0233 0%, #003eb7 100%);
  border: none;
}
.btn--blue:active:not(.focus-visible) {
  color: rgba(255, 255, 255, 0.7);
}
.btn--blue:disabled {
  color: #102654;
  background-image: none;
  border: 2px solid #1c3374;
  opacity: 0.3;
}
.btn--disabled-blue {
  color: #102654;
  background-image: none;
  border: 2px solid #1c3374;
  opacity: 0.3;
}

.custom-input,
.custom-textarea {
  position: relative;
  display: flex;
  width: 530px;
  padding: 14px 0 28px;
}
@media screen and (max-width: 767px) {
  .custom-input,
.custom-textarea {
    padding: 4px 0 48px;
    width: 100%;
  }
}
.custom-input label,
.custom-textarea label {
  display: flex;
  align-items: center;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .custom-input label,
.custom-textarea label {
    align-items: flex-start;
    flex-direction: column;
  }
}
.custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
  flex-grow: 1;
  max-width: 300px;
  min-height: 48px;
  margin-left: auto;
  padding: 10px;
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  outline: none;
  transition: border 0.3s ease;
}
@media screen and (max-width: 767px) {
  .custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
    max-width: unset;
    width: 100%;
  }
}
.custom-input label input:hover,
.custom-input label textarea:hover,
.custom-textarea label input:hover,
.custom-textarea label textarea:hover {
  border: 2px solid rgba(0, 0, 0, 0.5);
}
.custom-input label input:focus,
.custom-input label textarea:focus,
.custom-textarea label input:focus,
.custom-textarea label textarea:focus {
  border: 2px solid rgba(0, 0, 0, 0.8);
}
@media screen and (max-width: 767px) {
  .custom-input__label,
.custom-textarea__label {
    margin: 0 0 18px 0;
  }
}
.custom-input__error,
.custom-textarea__error {
  position: absolute;
  right: 0;
  bottom: 35px;
  color: #ff1553;
  opacity: 0;
  transition: opacity 0.3s ease;
}
@media screen and (max-width: 767px) {
  .custom-input__error,
.custom-textarea__error {
    bottom: 20px;
  }
}
.custom-input.is-invalid input,
.custom-input.is-invalid textarea,
.custom-textarea.is-invalid input,
.custom-textarea.is-invalid textarea {
  border: 2px solid #ff1553;
}
.custom-input.is-invalid input:focus, .custom-input.is-invalid input:hover,
.custom-input.is-invalid textarea:focus,
.custom-input.is-invalid textarea:hover,
.custom-textarea.is-invalid input:focus,
.custom-textarea.is-invalid input:hover,
.custom-textarea.is-invalid textarea:focus,
.custom-textarea.is-invalid textarea:hover {
  border: 2px solid rgba(255, 21, 83, 0.5);
}
.custom-input.is-valid input,
.custom-textarea.is-valid input {
  border: 2px solid #22b259;
}
.custom-input.is-valid input:focus, .custom-input.is-valid input:hover,
.custom-textarea.is-valid input:focus,
.custom-textarea.is-valid input:hover {
  border: 2px solid rgba(34, 178, 89, 0.5);
}
.custom-input .input-message,
.custom-textarea .input-message {
  position: absolute;
  right: 0;
  bottom: -5px;
}
.custom-input .input-message.is-invalid,
.custom-textarea .input-message.is-invalid {
  color: #ff0000;
}
.custom-input .input-message.is-valid,
.custom-textarea .input-message.is-valid {
  color: #15ab07;
}

@media (hover: hover) {
  .logo:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.logo.focus-visible:focus {
  opacity: 0.8;
}
.logo:active:not(.focus-visible) {
  opacity: 0.5;
}

.link {
  position: relative;
  text-decoration: underline;
  text-transform: uppercase;
  text-underline-offset: 5px;
}
.link::after {
  content: "";
  position: absolute;
  height: 150%;
  width: 150%;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}
.link--on-black {
  color: #ffffff;
  text-decoration: none;
  text-transform: none;
}
@media (hover: hover) {
  .link--on-black:hover:not(.focus-visible) {
    text-decoration: underline;
    text-underline-offset: 8px;
  }
}
.link--on-black.focus-visible:focus {
  outline: 1px solid #ffffff;
  text-decoration: none;
}
.link--on-black:active:not(.focus-visible) {
  text-decoration: none;
}
.link--disabled {
  pointer-events: none;
  opacity: 0.4;
}

.header {
  position: absolute;
  top: 40px;
  width: 100%;
}
@media (max-width: 767px) {
  .header {
    top: 20px;
  }
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  z-index: 1;
}
.header__phone {
  z-index: 1;
  font-weight: 600;
  font-size: 26px;
  line-height: normal;
  font-family: "Alumni Sans", "Gill Sans", "Arial", sans-serif;
  color: #ffffff;
  letter-spacing: 0.78px;
}
@media (max-width: 1199px) {
  .header__phone {
    font-weight: 700;
    font-size: 24px;
    letter-spacing: 0.48px;
  }
}
@media (max-width: 767px) {
  .header__phone {
    font-weight: 600;
    font-size: 20px;
    letter-spacing: 0.4px;
  }
}
@media (hover: hover) {
  .header__phone:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.header__phone.focus-visible:focus {
  opacity: 0.8;
}
.header__phone:active:not(.focus-visible) {
  opacity: 0.5;
}

.footer {
  display: flex;
  align-items: center;
  min-height: 108px;
  background-color: #030a1a;
}
@media (max-width: 1199px) {
  .footer {
    min-height: 114px;
  }
}
@media (max-width: 767px) {
  .footer {
    min-height: auto;
  }
}
.footer__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 767px) {
  .footer__wrapper {
    display: block;
    margin: 0 auto;
    max-width: 350px;
    padding: 50px 0;
    column-count: 2;
  }
}
@media (max-width: 499px) {
  .footer__wrapper {
    max-width: none;
    margin: 0;
  }
}
@media (max-width: 767px) {
  .footer__logo {
    display: inline-block;
    margin: 5px 0 15px 0;
  }
}
.footer__nav-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
  gap: 40px;
}
@media (max-width: 1199px) {
  .footer__nav-list {
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .footer__nav-list {
    display: block;
    margin: 0 0 40px 0;
  }
}
.footer__nav-link {
  font-size: 14px;
  line-height: normal;
  color: #ffffff;
}
@media (max-width: 767px) {
  .footer__nav-link {
    display: inline-block;
    font-size: 16px;
  }
}
@media (max-width: 767px) {
  .footer__nav-item {
    margin-bottom: 16px;
  }
}
.footer__social-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
  gap: 11px;
}
@media (max-width: 767px) {
  .footer__social-list {
    column-span: all;
  }
}
.footer__social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  background-color: #ffffff;
  border-radius: 50%;
}
@media (hover: hover) {
  .footer__social-link:hover:not(.focus-visible) {
    background-color: #103aae;
  }
  .footer__social-link:hover:not(.focus-visible) svg {
    fill: #ffffff;
  }
}
.footer__social-link.focus-visible:focus {
  background-color: #103aae;
  outline: none;
}
.footer__social-link.focus-visible:focus svg {
  fill: #ffffff;
}
.footer__social-link:active:not(.focus-visible) {
  background-color: #102654;
}
.footer__social-link--disabled {
  color: #030a1a;
  background-color: rgba(255, 255, 255, 0.3);
  pointer-events: none;
}
.footer__social-link svg {
  fill: #030a1a;
}

.hero {
  padding: 51px 0;
  background-image: conic-gradient(from 182deg at 33% 34%, #003D85 0deg, #B80027 313deg, #003D85 360deg);
}
@media (max-width: 1199px) {
  .hero {
    padding: 82px 0;
  }
}
@media (max-width: 767px) {
  .hero {
    padding: 115px 0 80px 0;
  }
}
.hero__wrapper {
  position: relative;
  display: grid;
  justify-content: center;
  gap: 65px;
  grid-template-columns: min-content 500px;
}
@media (max-width: 1199px) {
  .hero__wrapper {
    justify-content: center;
    gap: 30px;
    grid-template-columns: min-content 324px;
  }
}
@media (max-width: 899px) {
  .hero__wrapper {
    justify-content: end;
  }
}
@media (max-width: 767px) {
  .hero__wrapper {
    display: flex;
    flex-direction: column;
  }
}
.hero__wrapper picture {
  align-self: auto;
}
@media (max-width: 1349px) {
  .hero__wrapper picture {
    align-self: flex-end;
  }
}
@media (max-width: 1199px) {
  .hero__wrapper picture {
    align-self: auto;
  }
}
.hero__wrapper img {
  width: 500px;
}
@media (max-width: 1349px) {
  .hero__wrapper img {
    width: 420px;
  }
}
@media (max-width: 1199px) {
  .hero__wrapper img {
    width: 324px;
  }
}
